.contactListItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

li button {
  padding: 2px 20px;
  margin-left: 20px;
  width: 100px;

  background-color: rgb(248, 248, 248);
  border: none;
  border-radius: 5px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
    0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);

  line-height: 1.5;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 450ms cubic-bezier(0.4, 0, 0.2, 1),
    color 450ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 450ms cubic-bezier(0.4, 0, 0.2, 1);
}

button:hover,
button:focus {
  background-color: #C4E0E5;
  transform: scale(0.95);
}